.grid{
    display: grid;
    grid-template-columns: 33% 33% 33%;
    grid-template-rows: 1fr;
    justify-content: center;
    margin: 80px 60px;
}

.title{
    color: #6d6d6d;
    padding: 0;
    font-size: 1.4rem;
}

p {
    padding: 20px 0;
    font-size: 1.2rem;
    margin: 0 20px;
}